<template>
  <div>
    <fTitle>
      <template #title>Statistic 统计数值</template>
      <template #introduce>用于突出某个或某组数字时，如显示数值，如金额，排名等。倒计时模式</template>
    </fTitle>
    <sTitle>
      <template #title>基础用法和倒计时</template>
      <template #introduce>整体的时间（添加的时间量和原定时间）必须是未来的时间节点，否则依旧是倒计时结束</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="6" v-for="item in dataList" :key="item.title">
<div class="grid-content">
            <el-statistic
            :title="item.title"
            group-separator=","
            :precision="2"
            >
            <template slot="formatter">
              {{ item.data + (like ? 1 : '') }}
            </template>
            <template slot="prefix">
              <i class="el-icon-s-flag" style="color: red"></i>
            </template>
            <template slot="suffix">
              <span @click="like = !like" class="like">
                <i
                  class="el-icon-star-on"
                  style="color:red"
                  v-show="!!like"
                ></i>
                <i class="el-icon-star-off" v-show="!like"></i>
              </span>
            </template>
          </el-statistic>
</div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <el-card shadow="hover" style="width: 100%;">
          <div slot="header" class="clearfix">
            <span>文嘉《明日歌》</span>
            <el-button
              style="float: right; padding: 3px 0"
              type="text"
              @click="clickFn"
              >暂停</el-button
            >
          </div>
          <div style="font-size: 18px;text-align: center; margin-top: 35px;">
            明日复明日
          </div>
          <div style="font-size: 18px;text-align: center;">明日何其多</div>
          <div style="font-size: 18px;text-align: center;">我生待明日</div>
          <div style="font-size: 18px;text-align: center;">万事成蹉跎</div>
          <div style="margin-top: 35px;"></div>
          <el-statistic
            ref="statistic"
            @finish="hilarity"
            format="HH:mm:ss"
            :value="deadline4"
            title="距离明日："
            time-indices
          >
          </el-statistic>
        </el-card>
</div></el-col></el-row>
    </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      deadline4: Date.now() + (new Date().setHours(23, 59, 59) - Date.now()),
      like: false,
      dataList: [
        {
          title: '增长人数',
          data: (1, 314.00)
        },
        {
          title: '男女比',
          data: 456 / 2
        },
        {
          title: '增长人数',
          data: (4, 154.56)
        },
        {
          title: 'Feedback',
          data: 520
        }
      ]
    }
  },
  methods: {
    clickFn () {
      this.$refs.statistic.suspend(this.stop)
      this.stop = !this.stop
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;}
    }}
</style>
